import React, { useState } from 'react';
import { Button, Modal, Box, Typography, Checkbox, FormControlLabel } from '@mui/material';

const MailListSignUpComponent = () => {
    const [openModal, setOpenModal] = useState(false);
    const [agreed, setAgreed] = useState(false);

    const handleOpenModal = () => setOpenModal(true);
    const handleCloseModal = () => setOpenModal(false);
    const handleAgreeChange = (event: { target: { checked: boolean | ((prevState: boolean) => boolean); }; }) => setAgreed(event.target.checked);

    const handleSignup = async (email: string) => {
        if (!agreed) {
            alert("You must agree to the privacy policy!");
            return;
        }

        try {
            const response = await fetch(`/mail/emaillist`, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                    email: email, // Example payload, customize as needed
                }),
            });

            const data = await response.json();
            if (response.ok) {
                console.log('Signup successful:', data);
                handleCloseModal();
            } else {
                console.error('Signup failed:', data);
            }
        } catch (error) {
            console.error('Error during API call:', error);
        }
    };

    return (
        <div>
            {/* Signup Button */}
            <Button onClick={handleOpenModal} variant="contained" sx={{ width: { xs: "40vw", sm: "40vw", md: "40vw", lg: "40vw", xl: "30vw" }}}>
                Signup for the Newsletter
            </Button>

            {/* Modal */}
            <Modal
                open={openModal}
                onClose={handleCloseModal}
                aria-labelledby="signup-modal-title"
                aria-describedby="signup-modal-description"
            >
                <Box sx={{
                    position: 'absolute', top: '50%', left: '50%', transform: 'translate(-50%, -50%)',
                    bgcolor: 'gray', padding: 4, boxShadow: 24, borderRadius: 2, width: '25vw'
                }}>
                    <Typography id="signup-modal-title" variant="h6" component="h2">
                        Sign Up for the Newsletter
                    </Typography>
                    <Typography id="signup-modal-description" sx={{ mt: 2 }}>
                        By signing up, you agree to our <a href="/privacy-policy" target="_blank" rel="noopener noreferrer">Privacy Policy</a>.
                    </Typography>

                    {/* Checkbox for agreement */}
                    <FormControlLabel
                        control={<Checkbox checked={agreed} onChange={handleAgreeChange} />}
                        label="I agree to the Privacy Policy"
                    />

                    <div style={{ marginTop: 20, textAlign: 'center' }}>
                        <Button
                            variant="contained"
                            disabled={!agreed}
                            onClick={() => handleSignup("r.flinterman@projectsincluded.net")}
                        >
                            Confirm Signup
                        </Button>
                    </div>
                </Box>
            </Modal>
        </div>
    );
};

export default MailListSignUpComponent;
